/**
 * Stylesheet for general components
 */
.accessbility_hide {
  position: fixed;
  top: -10em;
}
.upload-video {
  margin-top: 1rem;
  width: 100%;
  height: 6rem;
  font-size: 1.5rem;}

.general-form {
  padding-left: 1rem;
  width: 100%;
  min-height: 10rem;
}

.end-padding {
  height: 5rem;
  width: 100%;
  position: relative;
}

.sidebar-dimmer {
  position: absolute;
  background: #4444445b;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

/* .MuiPaper-rounded, .ui.selection.dropdown, .ui.selection.dropdown .menu, .ui.selection .menu, .button, .ui.message, .modal, .card, .modal input, .modal textarea, .ui.segment, .ui.segments .segment {
  border-radius: 0 !important;
} */

.ui.selection.active.dropdown, .ui.selection.active.dropdown .menu {
  border-color: #fafafa !important;
  box-shadow: 0 0 10px rgba(4, 32, 32, 0.15) !important;
}
.ui.selection.active.dropdown .menu::-webkit-scrollbar {
  display: block;
  width: 6px;
}

.ui.multiple.dropdown>.label {
  background: rgb(230, 234, 235);
  color: rgb(95, 95, 95);}
  .ui.multiple.dropdown>.label:hover {
    background: rgb(226, 236, 238);
  }
/* .ui.dropdown .menu {
  border-color: rgb(22, 189, 180);
}
.ui.selection.active.dropdown {
  border-color: rgb(22, 189, 180) !important;
} */


.alert-primary {
  background: #228686 !important;
  color: white !important;
  font-size: 1.2em;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.alert-primary p {
  margin-left: 1rem;
}

.ui.secondary.button {
  background: var(--ct-btn-color) !important;
}
.ui.primary.button {
  background: var(--ct-btn-color) !important;
}

.plain-btn {
  background: transparent;
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
  text-align: left;
}

ul.plain-ul {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
}

/* No result class */
.no-results {
  width: 100%;
  text-align: center;
  color: grey;
}

.no-results.left {
  text-align: start;
}


.ct-media-poster {
  position: relative;
  height: -webkit-max-content;
  height: max-content;
  overflow: hidden; }
  .ct-media-poster.round {
    border-radius: 10px; }
  .ct-media-poster img.poster-img {
    position: relative;
    pointer-events: none;
    object-fit: cover;
    width: 100%; }
  .ct-media-poster .progress-bar {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 4px;
    width: 100%;
    background-color: rgba(114, 114, 114, 0.26); }
    .ct-media-poster .progress-bar .progress {
      height: 100%;
      border-radius: 0;
      background-color: #2daaaa; }
  .ct-media-poster .duration-box {
    position: absolute;
    right: 4px;
    bottom: 4px;
    height: 18px;
    padding: 1px 4px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.534);
    color: white;
    font-weight: bold;
    border-radius: 2px; }
    .ct-media-poster .duration-box.watched {
      bottom: 7px; }

.ct-media-card {
  display: flex;
  width: 100%;
  position: relative;
  overflow: hidden; }
  .ct-media-card .info-con {
    padding: 5px; }
  .ct-media-card:not(.row) {
    margin: 0 5px 10px 5px;
    flex-direction: column;
    width: 250px;
    min-width: 250px;
    height: 190px;
    min-height: 134px;
    border-radius: 5px; }
  .ct-media-card:not(.dark):hover {
    background-color: rgba(247, 248, 248, 0.664); }
  .ct-media-card.row {
    margin: 0;
    padding: 10px;
    white-space: nowrap;
    flex-wrap: nowrap;
    flex-direction: row; }
    .ct-media-card.row:not(:last-child) {
      border-bottom: 1px solid #ececec; }
    .ct-media-card.row .info-con {
      width: calc(100%);
      padding: 5px 10px 5px 15px; }
  .ct-media-card.dark {
    background: rgba(0, 0, 0, 0); }
    .ct-media-card.dark.row {
      border-color: #353535; }
    .ct-media-card.dark .info-con .media-name {
      color: white; }
    .ct-media-card.dark .info-con .media-description {
      color: #cecece; }
  .ct-media-card:hover .info-con .media-name, .ct-media-card:active .info-con .media-name {
    color: teal; }

/**
 * Stylesheet for General Modal
 */
.ct-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(63, 63, 63, 0.116);
  z-index: 2000; }
  .ct-modal .ct-mdl-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px); }
  .ct-modal .ct-mdl-con {
    background-color: white;
    border-radius: 5px;
    width: 30vw;
    min-width: 400px;
    box-shadow: 0 0 10px rgba(27, 27, 27, 0.089);
    overflow-x: hidden;
    overflow-y: auto;
    animation: ctmdl-show 50ms linear;
    -o-animation: ctmdl-show 50ms linear;
    -moz-animation: ctmdl-show 50ms linear;
    -webkit-animation: ctmdl-show 50ms linear;
    z-index: 1;
    display: none; }
    .ct-modal .ct-mdl-con.large {
      width: 60vw; }
    .ct-modal .ct-mdl-con.middle {
      width: 40vw;
      min-width: 400px; }
    .ct-modal .ct-mdl-con.ctmdl-open {
      display: block;
      animation: ctmdl-show 50ms linear;
      -o-animation: ctmdl-show 50ms linear;
      -moz-animation: ctmdl-show 50ms linear;
      -webkit-animation: ctmdl-show 50ms linear; }
    .ct-modal .ct-mdl-con.ctmdl-close {
      animation: ctmdl-hide 50ms linear;
      -o-animation: ctmdl-hide 50ms linear;
      -moz-animation: ctmdl-hide 50ms linear;
      -webkit-animation: ctmdl-hide 50ms linear; }
    .ct-modal .ct-mdl-con .ct-mdl-h-con {
      left: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: .5em .5em .5em 1em;
      background-color: white; }
      .ct-modal .ct-mdl-con .ct-mdl-h-con h3 {
        font-size: 1.8em;
        font-family: var(--ct-font-narrow);
        color: var(--ct-text-title);
        padding: 0;
        margin: 0vw;
        text-transform: uppercase; }
    .ct-modal .ct-mdl-con .ct-mdl-text-con {
      padding: .2em 1em;
      font-family: var(--ct-font-google);
      max-height: 500px;
      width: 100%;
      overflow: hidden; }
      .ct-modal .ct-mdl-con .ct-mdl-text-con .ct-mdl-text {
        width: 100%;
        overflow-x: hidden;
        overflow-y: auto; }
    .ct-modal .ct-mdl-con .ct-mdl-act {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      padding: 1em;
      background-color: white; }

@keyframes ctmdl-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0; }
  70% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    opacity: .2; }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1; } }

@-webkit-keyframes ctmdl-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0; }
  70% {
    -webkit-transform: scale(0.8);
