  width: 100%;
  height: 30px;
  border: 0;
  color: transparent;
  background: transparent; }

.volume-slider::-ms-fill-lower {
  background: linear-gradient(to bottom, var(--ct-green-active), var(--ct-green-active)) 100% 50%/100% 3px no-repeat transparent; }

.volume-slider::-ms-fill-upper {
  background: linear-gradient(to bottom, #8a8a8a, #8a8a8a) 100% 50%/100% 3px no-repeat transparent; }

