.watch-time-display {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: var(--ct-font-google);
  color: var(--ct-text-white);
  font-size: 16px;
  height: 30px;
  margin-left: 1em; }

.watch-progress-bar-container {
  position: absolute;
  top: 0;
  padding: 0 0 3px 0; }

.watch-progress-bar {
  position: relative;
  height: 4px;
  width: calc(100vw - 20px);
  overflow: visible;
  cursor: pointer; }

/* Buffer */
.buffered {
  height: 100%;
  position: relative;
  background: rgba(85, 85, 85, 0.555);
  width: 100%;
  border-radius: 5px; }

#buffered-amount {
  display: block;
  height: 100%;
  background-color: #777;
  width: 0;
  border-radius: 5px; }

/* Progress */
.watch-progress-bar .progress {
  margin-top: -4px;
  height: 100%;
  position: relative;
  width: 100%;
  background: transparent;
  border-radius: 5px;
  overflow: visible;
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 1; }

#progress-amount {
  display: block;
  height: 100%;
  background-color: #13d3d3;
  width: 0;
  border-radius: 5px; }

/* Seeking */
.seeking {
  margin-top: -4px;
  height: 100%;
  position: relative;
  width: 100%;
  background: transparent;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  overflow: hidden; }

#seeking-to {
  display: block;
  height: 100%;
  background-color: rgba(0, 161, 167, 0.39);
  width: 0;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px; }

#seeking-time {
  position: absolute;
  top: -35px;
  padding: .5em 1em;
  display: flex;
  opacity: 0;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: #2f2f2f;
  border-radius: 20px;
  width: 70px;
  height: auto; }

/* Scrubber */
.watch-progress-bar .progress .end-circle {
  background: #13d3d3;
  width: 14px;
  height: 14px;
  border-radius: 100%;
  margin-left: -7px;
  opacity: 0;
  cursor: pointer !important; }

.watch-progress-bar .progress .end-circle-ghost {
  background: #13d3d3;
  width: 14px;
  height: 14px;
  border-radius: 100%;
  margin-left: -14px;
  opacity: 0;
  cursor: pointer !important; }

/* Animations */
.watch-progress-bar-container:hover {
  padding-bottom: 0; }
  .watch-progress-bar-container:hover .watch-progress-bar {
    height: 7px; }
  .watch-progress-bar-container:hover .seeking {
    margin-top: -7px; }
  .watch-progress-bar-container:hover .progress {
    margin-top: -7px; }
    .watch-progress-bar-container:hover .progress .end-circle {
      opacity: 1; }

/* Mobile */
.watch-progress-bar-container[data-mobile=true] {
  padding-bottom: 0; }
  .watch-progress-bar-container[data-mobile=true] .watch-progress-bar {
    height: 4px; }
  .watch-progress-bar-container[data-mobile=true] .seeking {
    margin-top: -4px; }
  .watch-progress-bar-container[data-mobile=true] .progress {
    margin-top: -4px; }
    .watch-progress-bar-container[data-mobile=true] .progress .end-circle {
      opacity: 1; }
  .watch-progress-bar-container[data-mobile=true] #seeking-to {
    display: none; }
  .watch-progress-bar-container[data-mobile=true] #seeking-time {
    display: none; }

.watch-trans-container-web {
  position: fixed;
  overflow-x: hidden;
  overflow-y: auto !important;
  scroll-behavior: smooth;
  background-color: black;
  z-index: 99;
  height: 100%;
  top: 0;
  left: 62vw;
  width: 38vw; }

.watch-trans-container-mobile {
  position: fixed;
  overflow-x: hidden;
  overflow-y: auto !important;
  scroll-behavior: smooth;
  background-color: black;
  z-index: 99;
  height: 55vh;
  top: 45vh;
  bottom: 0;
  width: 100%; }

.trans-box {
  position: relative;
  width: calc(100% - 3vw);
  min-height: 100%;
  padding-top: calc(50%);
  padding-bottom: calc(50% + 5em); }

.trans-list {
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column; }

.trans-article {
  position: relative;
  width: 100%;
  height: auto;
  display: block; }

.watch-trans-container[mode=theatre-mode] {
  display: none; }

.watch-trans-container[mode=ps-mode] .trans-box {
  padding-top: calc(50% + 10em); }

.watch-trans-container[mode=bulk-edit-mode] {
  left: 37em;
  width: calc(100vw - 37em);
  min-width: 60em; }
  .watch-trans-container[mode=bulk-edit-mode] .trans-box {
    position: relative;
    width: 100%;
    padding: 1em 2em 20em 0;
    min-height: auto; }

.watch-trans-place-holder {
  width: 100%;
  margin-top: 5em; }

@media screen and (max-width: 900px) {
  .watch-trans-container {
    width: 38vw;
    top: 0;
    padding: 0 1em; }
  .trans-box {
    padding-top: calc(65%); }
  .trans-article {
    padding-left: 1em; } }

